.special-promo {
  box-sizing: border-box;
  min-height: 289px;
  @include flex(column);
  justify-content: flex-end;
  align-items: center;

  background-color: $green;
  background: url('../img/icon-gift.svg'),
              url('../img/gift-photo-mobile@1x.jpg');
  background-repeat: no-repeat, no-repeat;
  background-size: 56px 61px, auto;
  background-position: 50% 28%, 0;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background: url('../img/icon-gift.svg'),
                url('../img/gift-photo-mobile@2x.jpg');
    background-repeat: no-repeat, no-repeat;
    background-size: 56px 61px, 280px auto;
    background-position: 50% 28%, 0;
  }
}

.special-promo__about {
  width: 161px;
  margin: 0;
  margin-bottom: 45px;
  font-family: $secondary-font;
  @include text(16px, 20px);
  color: $white;
}

@media(min-width: $tablet-width) {
  .special-promo {
    min-height: 200px;
    padding: 0;
    padding-right: 125px;
    justify-content: center;
    align-items: flex-end;

    background-color: $green;

    background: url('../img/icon-gift.svg'),
                url('../img/gift-photo-tablet@1x.jpg');
    background-repeat: no-repeat;
    background-size: 56px 61px, auto;
    background-position: 14% 47%, 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background: url('../img/icon-gift.svg'),
                  url('../img/gift-photo-tablet@2x.jpg');
      background-repeat: no-repeat;
      background-size: 56px 61px, 708px auto;
      background-position: 14% 47%, 0;
    }
  }

  .special-promo__about {
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .special-promo {
    box-sizing: border-box;
    min-height: 289px;
    width: 245px;
    padding: 0;
    @include flex(column);
    justify-content: flex-end;
    align-items: center;

    background-color: $green;
    background: url('../img/icon-gift.svg'),
                url('../img/gift-photo-desktop@1x.jpg');
    background-repeat: no-repeat, no-repeat;
    background-size: 56px 61px, auto;
    background-position: 50% 28%, 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background: url('../img/icon-gift.svg'),
                  url('../img/gift-photo-desktop@2x.jpg');
      background-repeat: no-repeat, no-repeat;
      background-size: 56px 61px, 245px auto;
      background-position: 50% 28%, 0;
    }
  }

  .special-promo__about {
    margin-bottom: 41px;

    text-align: center;
  }
}
